/* 1d2026 */

#start {
  position:relative;
  display: block;
  width: calc(100vw - 150px);
  height: 70vh;
  top: 100px;
  
}

body {
  background-color: #1c1c1c;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  top:0;
  margin:0;
  overflow-x:hidden;
}

h2 {
  z-index:11;
  font-family:'Poppins';
  font-size:92px;
  color:black;
  display:inline-block;
  padding:0;
  margin:0px;
  top:30px;
  left:40px;
  position: absolute;
  -webkit-user-select: none;
}

/* Menlo Font to be use on site */

.mono {font-family: Menlo;}


/* [DOCUMENTATION] text */

#doc {
  font-size:12px;
  position: absolute;
  top:10px;
  right: 10px;
  z-index:11;
  margin:0;
  color:rgba(0, 0, 0, 0.5)
}

/* software engineer. text */

#SWE {
  font-size:20px;
  color:rgba(0, 0, 0, 1);
  position:absolute;
  z-index: 13;
  top: 150px;
  left: 40px;
  margin:0;
}

/* [scroll to proceed] label */

#label {
  position: absolute;
  color: rgba(0, 0, 0, 0.5);
  z-index:11;
  font-size: 12px;
  margin: 0;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
}

/* intial background gradient */

#top-gradient {
  position: absolute;
  width: 100%;
  height:100%;
  top: 0px;
  margin:0;
  background: linear-gradient(155deg, #FFE600 1.26%, #00FBFF 97.48%);
  background-size: 200% 100%;
  z-index:10;
  -webkit-animation: move 5s ease infinite;
          animation: move 5s ease infinite;
}


/* Top Gradient Movement */

@-webkit-keyframes move {
	0% {background-position: 0% 50%;}
	50% {background-position: 100% 50%;}
	100% {background-position: 0% 50%;}
}

@keyframes move {
	0% {background-position: 0% 50%;}
	50% {background-position: 100% 50%;}
	100% {background-position: 0% 50%;}
}

/* Text Highlight Color */

::selection {
  background:rgba(255, 255, 255, 0.2);
}

/* Responsive Design Rules */

@media only screen and (max-width: 600px) {
  h2 {
    font-size: 15vw;
  } 
}

/* Outer Container */

#about-me {
    z-index:7;
    width: calc(100vw - 600px);
    position:relative;
    display: block;
    padding:auto;
    padding-top:30px;
    padding-bottom:30px;
    top:40px;
}

#top-header {
    padding-top:30px;
    z-index:9;
}

#about-me-body {
    border: 1px solid rgba(255, 255, 255, 0.15);
    display: flex;
    flex-direction: column;
    min-height: 380px;
}

/* Text */

#about-me-text {
    width: calc(100% - 400px); 
    display:inline-block;
    padding-left:60px;
    padding-right:60px;
    padding-top: 20px;
    padding-bottom: 20px;
    font-size: 16px;  
}

/* Portrait (temporary substitute, I'll replace with a picture when I can get a rood one.) */

#portrait {
    width: 200px;
    height: 250px;
    background-color: black;
    margin:0;
    /* margin-top:15px; */
    position: absolute;
    right:65px;
    /* display: inline-block; */
    top: 340px;
    -webkit-transform: translateY(-50%);
    transform: translateY(-50%);

}

/* "Columbia University" Text */

#columbia {
    color: #5cbcf2;
