#experience-card {
    position:relative;
    display:flex;
    flex-direction:column;
    position: relative;
    padding:25px;
    z-index:8;
    background-color:#171717;
    border: 0px solid;
    font-family: Inter;
    margin:0;
    margin-bottom:20px;
}

#experience-card::selection {
    background:#c0c0c0;
    color: #1c1c1c;
}

h5 {
    font-size:24px;
    position: absolute;
    top:35px;
    margin-top:7px;
    margin-bottom:5px;
    left:35px;
}

#cards {
    display:block;
}

.positions {
    display:block;
    margin-top:8px;
    margin-bottom:8px;
    padding-left:70px;
    font-size:22px;
    color:#fff;
    font-family: Menlo;
    left:40px;
}

.description {
    /* margin-left: 0px; */
    margin-top: 10px;
    margin-bottom: 10px;
    font-family: Menlo;
    color: rgba(255, 255, 255, 0.7);
    display: block;
}

#CU {
    color: #5cbcf2;
    font-weight:600;
}

@media only screen and (max-width: 820px) {
   #experience-card {
    left: 50%;
    -moz-transform: translateX(-50%);
    -webkit-transform: translateX(-50%);
    transform: translateX(-50%);
   }
}