.filler {
    /* background-color: rgb(20, 20, 20); */
    width: calc((100vw - 600px - 60px) / 3);
    height: calc((100vw - 600px - 60px) / 3);
    position: relative;
    margin:0;
    display:inline-block;
}

#ellipsis {
    -moz-user-select: none;
    -khtml-user-select: none;
    -webkit-user-select: none;
    -ms-user-select: none;
    user-select: none;
}


@media only screen and (max-width: 1350px) {
    
    .filler {
        width: calc((100vw - 400px - 60px) / 2);
        height: calc((100vw - 400px - 60px) / 2);
        display:inline-block;
    }

}

@media only screen and (max-width: 820px) {
    .filler {
        width: calc((100vw - 50px - 60px) / 2);
        height: calc((100vw - 50px - 60px) / 2);
    }
}

@media only screen and (max-width: 450px) {
    .filler {
        width: calc(100vw - 50px - 60px);
        height: calc(100vw - 50px - 60px);
    }
}